













































import {Component, Prop, Vue} from 'vue-property-decorator';
import PreviewElementSmartRenderer from "@/components/BuilderUI/PreviewElementSmartRenderer.vue";
import PreviewBox from "@/components/BuilderUI/PreviewBox.vue";
import {CRUD} from "@/api/CRUD";
import {Category, Request} from "@/api/URI";
import {Auth} from "@/api/auth/Auth";
import {OMS} from "@/OMS";
import {Media} from "@/template/Media";

@Component({
  components: {PreviewBox, PreviewElementSmartRenderer},
})
export default class TestView extends Vue {

  get user() { return Auth.getUser(); }

  mediaList: Array<Media> = [];

  mounted() {
    this.getAll();
  }

  async getAll() {
    const crud = new CRUD(Category.MEDIA);
    crud.setRequest(Request.GET);
    await crud.shoot({});
    this.mediaList = crud.getResult();
    crud.log();
  }
}
